import interfaces from '../../utils/interface.js';
import jifenHead from './base/head.vue'
import jifenList from './base/jifenList.vue'
export default {
	data() {
		return {
			jifen : 20,//积分
			categoryList: [], //分类列表
			goodsList: [], //商品列表
			animate: false,
			marqueeList: [{
					name: '130****3195',
					city: '北京',
					amount: '10'
				},
				{
					name: '150****2454',
					city: '上海',
					amount: '20'
				},
				{
					name: '131****2329',
					city: '广州',
					amount: '30'
				},
				{
					name: '183****5685',
					city: '重庆',
					amount: '40'
				}
			]
		}
	},
	components: {
		jifenHead,
		jifenList
	},
	onLoad() {
		this.initData();
		// this.$refs.Series.getTime()
	},
	created() {
		setInterval(this.showMarquee, 5000)
	},
	methods: {
		showMarquee() {
			this.animate = true;
			setTimeout(() => {
				this.marqueeList.push(this.marqueeList[0]);
				this.marqueeList.shift();
				this.animate = false;
			}, 1000)
		},
		// ceshi(e) {
		// 	console.log('456', e)
		// 	this.$refs.Series.getTime()
		// },
		async initData() {
			this.request({
				url: interfaces.get_bigclass, //获取分类名称
				success: ((res) => {
					if (res.error_code == 0) {
						this.categoryList = res.data;
					}
				})
			});
			this.request({
				url: interfaces.shoping_shop, //积分商城首页数据
				success: ((res) => {
					console.log(res)
					if (res.error_code == 0) {
						this.goodsList = res.data;
					}
				})
			});
			this.request({
				url: interfaces.get_jifen, //获取用户积分
				method: "POST",
				success: ((res) => {
					this.jifen = res.data;
				})
			});
		},
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh();
			this.initData();
		}, 1000);
	},
}
